.col1 {
    display: flex;
    flex-direction: column;
    width: 19.5vw;
    gap: 1.5vh;
}

.item {
    height: fit-content;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 8px;
    padding: 2.2vh;
    text-align: left;
}

.item article {
    font-family: 'Nunito Sans', sans-serif;
    font-size: 1rem;
    line-height: 2.5vh;
    color: #0B4141;
    margin-bottom: 1.25vh;
}

.about {
    display: flex;
    flex-direction: column;
    font-size: 0.8rem;
    line-height: 2vh;
}

article::selection, .item section div::selection, .item section img::selection {
    background: #FFCCBC;
}

 section img {
    width: 2.3vw;
}

.item section {
    display: flex;
    flex-direction: row;
    gap: 1.3vw;
}