.sec5_article {
    width: 70vw;
    padding: 20vh 0;
    margin-right: auto;
    margin-left: auto;
}

.sec5_article h1 {
    font-family: 'Motter Corpus Std', sans-serif;
    font-size: 4rem;
    line-height: 11vh;
    letter-spacing: 3px;
    text-align: center;
    color: #FFCCBC;
    margin: 0;
}

.sec5_article h1::selection{
    color: #0b4141;
}

.sec5_article div::selection, .sec5_article span::selection{
    background: #FFCCBC;
    color: #0b4141;
}

.sec5_article div{
    margin: 4vh 4.6vw 0 4.6vw;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 1.5rem;
    line-height: 4vh;
    text-align: center;
    color: #0B4141;
}

.sec5_article span {
    font-weight: bold;
}









