.sec3_article {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 31vw;
    margin-left: 4vw;
}

.sec3_article h1 {
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 900;
    font-size: 4rem;
    line-height: 10vh;
    color: #FFCCBC;
    margin: 15vh 0 0 0;
    text-align: left;
}

.sec3_article h1::selection, .sec3_article div::selection, .sec3_article button::selection {
    background: #FFCCBC;
    color: #0b4141;
}

.sec3_article div {
    font-family: 'Nunito Sans', sans-serif;
    font-size: 1.5rem;
    line-height: 4vh;
    color: #FFCCBC;
    text-align: left;
}

.sec3_article button {
    text-align: left;
    box-sizing: border-box;
    padding: 2vh 1.7vw;
    border: 2px solid #FFCCBC;
    border-radius: 100px;
    background: transparent;
    color: #FFCCBC;
    font-size: 1.5rem;
    width: fit-content;
}

.sec3_article button:active {
    background: #FFCCBC;
    color: #0b4141;
}