.sec7_footer {
    align-items: flex-start;
    padding-left: 3vw;
    margin-top: 9vh;
    display: flex;
    flex-direction: column;
}

.sec7_footer div:first-of-type {
    font-family: 'Motter Corpus Std', sans-serif;
    font-size: 2rem;
    line-height: 5vh;
    color: #FFCCBC;
}

.sec7_footer *::selection {
    background: #FFCCBC;
    color: #0b4141;
}

.sec7_footer div:last-of-type {
    font-family: 'Nunito Sans', sans-serif;
    font-size: 1.2rem;
    line-height: 3vh;
    color: #FFCCBC;
}