.firstrow_article {
    padding-left: 6vw;
    padding-right: 6vw;
}

.firstrow_article h2 {
    font-family: 'Nunito Sans', sans-serif;
    font-weight: bolder;
    font-size: 4rem;
    line-height: 9vh;
    color: #FFCCBC;
    text-align: left;
}

.firstrow_article h2::selection {
    color: #0b4141;
}

.firstrow_article div {
    color: #0B4141;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 1.5rem;
    line-height: 4vh;
    text-align: left;
}

.firstrow_article div::selection, .scroll div::selection, .scroll img::selection {
    background: #FFCCBC;
}

.scroll {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 50px;
    gap: 20px;
}
.scroll img {
    width: 2vh;
    height: 3vh;
}

.scroll div {
    text-transform: uppercase;
    font-size: 1.5rem;
}
