.sec2_article {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20vh 0;
    position: relative;
    z-index: 3;
    box-sizing: border-box;
}

.sec2_article h1 {
    display: block;
    font-family: 'Motter Corpus Std', sans-serif;
    font-size: 4.5rem;
    line-height: 13vh;
    color: white;
    margin-bottom: 0;
    letter-spacing: 5px;
}

.sec2_article h1::selection {
    color: #0b4141;
}

.sec2_article div {
    font-family: 'Nunito Sans', sans-serif;
    font-size: 1.5rem;
    line-height: 4vh;
    color: white;
    margin-top: 2vh;
    width: 37vw;
}

.sec2_article div::selection {
    color: #0b4141;
}

.sec2_article span {
    font-weight: bold;
}

.sec2_article span::selection {
    color: #0b4141;
}