.secondRow_article {
    padding-left: 6vw;
    padding-right: 6vw;
}

.secondRow_article h2{
    font-family: 'Nunito Sans', sans-serif;
    font-weight: bolder;
    font-size: 4rem;
    line-height: 9vh;
    color: #0B4141;
    text-align: left;
}

.secondRow_article h2::selection, .secondRow_article div::selection {
    background: #FFCCBC;
}

.secondRow_article div {
    color: #0B4141;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 1.5rem;
    line-height: 4vh;
    text-align: left;

}